import React, { useState, useRef, useEffect } from 'react';
import {
  View,
  Text,
  StyleSheet,
  TouchableOpacity,
  StatusBar,
  SafeAreaView,
  Animated,
  KeyboardAvoidingView,
  Platform,
  ScrollView,
  Alert,
} from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { NativeStackNavigationProp } from '@react-navigation/native-stack';
import { AuthStackParamList } from '../../types/navigation';
import { colors, spacing, fonts, borderRadius, shadows } from '../../constants/theme';
import { AuthInput } from '../../components/common/AuthInput';
import { AuthButton } from '../../components/common/AuthButton';
import { LoadingScreen } from '../../components/common/LoadingScreen';
import { authService } from '../../services/authService';

type ForgotPasswordScreenNavigationProp = NativeStackNavigationProp<
  AuthStackParamList,
  'ForgotPassword'
>;

type Step = 'email' | 'verification' | 'reset';

interface FormData {
  email: string;
  verificationCode: string;
  newPassword: string;
  confirmPassword: string;
}

interface FormErrors {
  email?: string;
  verificationCode?: string;
  newPassword?: string;
  confirmPassword?: string;
  general?: string;
}

export const ForgotPasswordScreen: React.FC = () => {
  const navigation = useNavigation<ForgotPasswordScreenNavigationProp>();

  const [currentStep, setCurrentStep] = useState<Step>('email');
  const [formData, setFormData] = useState<FormData>({
    email: '',
    verificationCode: '',
    newPassword: '',
    confirmPassword: '',
  });
  const [errors, setErrors] = useState<FormErrors>({});
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);

  // Âä®ÁîªÂÄº
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const slideAnim = useRef(new Animated.Value(30)).current;
  const shakeAnim = useRef(new Animated.Value(0)).current;
  const stepProgressAnim = useRef(new Animated.Value(0)).current;

  useEffect(() => {
    // ÂêØÂä®ÂÖ•Âú∫Âä®Áîª
    Animated.parallel([
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: true,
      }),
      Animated.timing(slideAnim, {
        toValue: 0,
        duration: 600,
        useNativeDriver: true,
      }),
    ]).start();
  }, []);

  useEffect(() => {
    // Êõ¥Êñ∞Ê≠•È™§ËøõÂ∫¶Âä®Áîª
    const stepValue = currentStep === 'email' ? 0 : currentStep === 'verification' ? 0.5 : 1;
    Animated.timing(stepProgressAnim, {
      toValue: stepValue,
      duration: 300,
      useNativeDriver: false,
    }).start();
  }, [currentStep]);

  useEffect(() => {
    // ÂÄíËÆ°Êó∂ÈÄªËæë
    let timer: ReturnType<typeof setTimeout>;
    if (countdown > 0) {
      timer = setTimeout(() => setCountdown(countdown - 1), 1000);
    }
    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [countdown]);

  // Ë°®ÂçïÈ™åËØÅ
  const validateEmail = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.email.trim()) {
      newErrors.email = 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±Âú∞ÂùÄ';
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÈÇÆÁÆ±Âú∞ÂùÄ';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validateVerificationCode = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.verificationCode.trim()) {
      newErrors.verificationCode = 'ËØ∑ËæìÂÖ•È™åËØÅÁ†Å';
    } else if (formData.verificationCode.length !== 6) {
      newErrors.verificationCode = 'È™åËØÅÁ†ÅÂ∫î‰∏∫6‰ΩçÊï∞Â≠ó';
    } else if (!/^\d{6}$/.test(formData.verificationCode)) {
      newErrors.verificationCode = 'È™åËØÅÁ†ÅÂè™ËÉΩÂåÖÂê´Êï∞Â≠ó';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const validatePassword = (): boolean => {
    const newErrors: FormErrors = {};

    if (!formData.newPassword) {
      newErrors.newPassword = 'ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å';
    } else if (formData.newPassword.length < 8) {
      newErrors.newPassword = 'ÂØÜÁ†ÅËá≥Â∞ëÈúÄË¶Å8‰ΩçÂ≠óÁ¨¶';
    } else if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d)/.test(formData.newPassword)) {
      newErrors.newPassword = 'ÂØÜÁ†ÅÈúÄË¶ÅÂåÖÂê´Â§ßÂ∞èÂÜôÂ≠óÊØçÂíåÊï∞Â≠ó';
    }

    if (!formData.confirmPassword) {
      newErrors.confirmPassword = 'ËØ∑Á°ÆËÆ§Êñ∞ÂØÜÁ†Å';
    } else if (formData.newPassword !== formData.confirmPassword) {
      newErrors.confirmPassword = '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  // ÈúáÂä®Âä®Áîª
  const triggerShakeAnimation = () => {
    Animated.sequence([
      Animated.timing(shakeAnim, {
        toValue: 10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: -10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: 10,
        duration: 100,
        useNativeDriver: true,
      }),
      Animated.timing(shakeAnim, {
        toValue: 0,
        duration: 100,
        useNativeDriver: true,
      }),
    ]).start();
  };

  // ÂèëÈÄÅÈ™åËØÅÁ†Å
  const handleSendVerificationCode = async () => {
    if (!validateEmail()) {
      triggerShakeAnimation();
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      await authService.forgotPassword({ email: formData.email.trim() });
      setCurrentStep('verification');
      setCountdown(60); // 60ÁßíÂÄíËÆ°Êó∂
      Alert.alert('È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅ', 'ËØ∑Êü•ÁúãÊÇ®ÁöÑÈÇÆÁÆ±Âπ∂ËæìÂÖ•È™åËØÅÁ†Å');
    } catch (error: any) {
      console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error.message);
      setErrors({ general: error.message || 'ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•ÔºåËØ∑ÈáçËØï' });
      triggerShakeAnimation();
    } finally {
      setIsLoading(false);
    }
  };

  // È™åËØÅÈ™åËØÅÁ†Å
  const handleVerifyCode = async () => {
    if (!validateVerificationCode()) {
      triggerShakeAnimation();
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      await authService.verifyResetCode({
        email: formData.email.trim(),
        code: formData.verificationCode.trim(),
      });
      setCurrentStep('reset');
    } catch (error: any) {
      console.error('È™åËØÅÁ†ÅÈ™åËØÅÂ§±Ë¥•:', error.message);
      setErrors({ general: error.message || 'È™åËØÅÁ†ÅÈîôËØØÔºåËØ∑ÈáçËØï' });
      triggerShakeAnimation();
    } finally {
      setIsLoading(false);
    }
  };

  // ÈáçÁΩÆÂØÜÁ†Å
  const handleResetPassword = async () => {
    if (!validatePassword()) {
      triggerShakeAnimation();
      return;
    }

    setIsLoading(true);
    setErrors({});

    try {
      await authService.resetPassword({
        email: formData.email.trim(),
        code: formData.verificationCode.trim(),
        newPassword: formData.newPassword,
      });

      Alert.alert(
        'ÂØÜÁ†ÅÈáçÁΩÆÊàêÂäü',
        'ÊÇ®ÁöÑÂØÜÁ†ÅÂ∑≤ÊàêÂäüÈáçÁΩÆÔºåËØ∑‰ΩøÁî®Êñ∞ÂØÜÁ†ÅÁôªÂΩï',
        [
          {
            text: 'ÂéªÁôªÂΩï',
            onPress: () => navigation.navigate('Login'),
          },
        ]
      );
    } catch (error: any) {
      console.error('ÂØÜÁ†ÅÈáçÁΩÆÂ§±Ë¥•:', error.message);
      setErrors({ general: error.message || 'ÂØÜÁ†ÅÈáçÁΩÆÂ§±Ë¥•ÔºåËØ∑ÈáçËØï' });
      triggerShakeAnimation();
    } finally {
      setIsLoading(false);
    }
  };

  // ÈáçÊñ∞ÂèëÈÄÅÈ™åËØÅÁ†Å
  const handleResendCode = async () => {
    if (countdown > 0) return;

    setIsLoading(true);
    try {
      await authService.forgotPassword({ email: formData.email.trim() });
      setCountdown(60);
      Alert.alert('È™åËØÅÁ†ÅÂ∑≤ÈáçÊñ∞ÂèëÈÄÅ', 'ËØ∑Êü•ÁúãÊÇ®ÁöÑÈÇÆÁÆ±');
    } catch (error: any) {
      Alert.alert('ÂèëÈÄÅÂ§±Ë¥•', error.message || 'ÈáçÊñ∞ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•');
    } finally {
      setIsLoading(false);
    }
  };

  // ËøîÂõû‰∏ä‰∏ÄÊ≠•
  const handleBack = () => {
    if (currentStep === 'email') {
      navigation.goBack();
    } else if (currentStep === 'verification') {
      setCurrentStep('email');
    } else {
      setCurrentStep('verification');
    }
  };

  // Êõ¥Êñ∞Ë°®ÂçïÊï∞ÊçÆ
  const updateFormData = (field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Ê∏ÖÈô§ÂØπÂ∫îÂ≠óÊÆµÁöÑÈîôËØØ
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }));
    }
  };

  // Ëé∑ÂèñÂΩìÂâçÊ≠•È™§‰ø°ÊÅØ
  const getStepInfo = () => {
    switch (currentStep) {
      case 'email':
        return {
          title: 'ÂøòËÆ∞ÂØÜÁ†Å',
          subtitle: 'ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈÇÆÁÆ±Âú∞ÂùÄÔºåÊàë‰ª¨Â∞ÜÂèëÈÄÅÈ™åËØÅÁ†Å',
          buttonText: 'ÂèëÈÄÅÈ™åËØÅÁ†Å',
          onPress: handleSendVerificationCode,
        };
      case 'verification':
        return {
          title: 'È™åËØÅÈÇÆÁÆ±',
          subtitle: `È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅËá≥ ${formData.email}`,
          buttonText: 'È™åËØÅ',
          onPress: handleVerifyCode,
        };
      case 'reset':
        return {
          title: 'ÈáçÁΩÆÂØÜÁ†Å',
          subtitle: 'ËØ∑ËÆæÁΩÆÊÇ®ÁöÑÊñ∞ÂØÜÁ†Å',
          buttonText: 'ÈáçÁΩÆÂØÜÁ†Å',
          onPress: handleResetPassword,
        };
    }
  };

  const stepInfo = getStepInfo();

  if (isLoading) {
    return <LoadingScreen message="Â§ÑÁêÜ‰∏≠..." />;
  }

  return (
    <SafeAreaView style={styles.container}>
      <StatusBar barStyle="dark-content" backgroundColor={colors.background} />
      
      <KeyboardAvoidingView
        style={styles.keyboardAvoid}
        behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
      >
        <ScrollView
          style={styles.scrollView}
          contentContainerStyle={styles.scrollContent}
          showsVerticalScrollIndicator={false}
          keyboardShouldPersistTaps="handled"
        >
          {/* Â§¥ÈÉ®Âå∫Âüü */}
          <Animated.View
            style={[
              styles.header,
              {
                opacity: fadeAnim,
                transform: [{ translateY: slideAnim }],
              },
            ]}
          >
            <TouchableOpacity style={styles.backButton} onPress={handleBack}>
              <Text style={styles.backButtonText}>‚Üê</Text>
            </TouchableOpacity>
            
            <View style={styles.titleContainer}>
              <Text style={styles.title}>{stepInfo.title}</Text>
              <Text style={styles.subtitle}>{stepInfo.subtitle}</Text>
            </View>

            {/* Ê≠•È™§ËøõÂ∫¶ÊåáÁ§∫Âô® */}
            <View style={styles.progressContainer}>
              <View style={styles.progressBar}>
                <Animated.View
                  style={[
                    styles.progressFill,
                    {
                      width: stepProgressAnim.interpolate({
                        inputRange: [0, 1],
                        outputRange: ['33%', '100%'],
                      }),
                    },
                  ]}
                />
              </View>
              <View style={styles.stepIndicators}>
                <View style={[styles.stepDot, styles.stepDotActive]}>
                  <Text style={styles.stepDotText}>1</Text>
                </View>
                <View style={[styles.stepDot, currentStep !== 'email' && styles.stepDotActive]}>
                  <Text style={[styles.stepDotText, currentStep !== 'email' && styles.stepDotTextActive]}>2</Text>
                </View>
                <View style={[styles.stepDot, currentStep === 'reset' && styles.stepDotActive]}>
                  <Text style={[styles.stepDotText, currentStep === 'reset' && styles.stepDotTextActive]}>3</Text>
                </View>
              </View>
            </View>
          </Animated.View>

          {/* Ë°®ÂçïÂå∫Âüü */}
          <Animated.View
            style={[
              styles.formContainer,
              {
                opacity: fadeAnim,
                transform: [
                  { translateY: slideAnim },
                  { translateX: shakeAnim },
                ],
              },
            ]}
          >
            {/* ÈîôËØØÊèêÁ§∫ */}
            {errors.general && (
              <View style={styles.errorContainer}>
                <Text style={styles.errorText}>{errors.general}</Text>
              </View>
            )}

            {/* Ê≠•È™§1: ÈÇÆÁÆ±ËæìÂÖ• */}
            {currentStep === 'email' && (
              <View style={styles.stepContent}>
                <AuthInput
                  label="ÈÇÆÁÆ±Âú∞ÂùÄ"
                  placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÈÇÆÁÆ±"
                  value={formData.email}
                  onChangeText={(value) => updateFormData('email', value)}
                  error={errors.email}
                  keyboardType="email-address"
                  autoCapitalize="none"
                  autoComplete="email"
                  icon="üìß"
                />
              </View>
            )}

            {/* Ê≠•È™§2: È™åËØÅÁ†ÅËæìÂÖ• */}
            {currentStep === 'verification' && (
              <View style={styles.stepContent}>
                <AuthInput
                  label="È™åËØÅÁ†Å"
                  placeholder="ËØ∑ËæìÂÖ•6‰ΩçÈ™åËØÅÁ†Å"
                  value={formData.verificationCode}
                  onChangeText={(value) => updateFormData('verificationCode', value)}
                  error={errors.verificationCode}
                  keyboardType="number-pad"
                  maxLength={6}
                  icon="üî¢"
                />

                {/* ÈáçÊñ∞ÂèëÈÄÅÈ™åËØÅÁ†Å */}
                <View style={styles.resendContainer}>
                  <Text style={styles.resendText}>
                    Ê≤°ÊúâÊî∂Âà∞È™åËØÅÁ†ÅÔºü{' '}
                  </Text>
                  <TouchableOpacity
                    onPress={handleResendCode}
                    disabled={countdown > 0}
                  >
                    <Text style={[styles.resendLink, countdown > 0 && styles.resendLinkDisabled]}>
                      {countdown > 0 ? `${countdown}sÂêéÈáçÊñ∞ÂèëÈÄÅ` : 'ÈáçÊñ∞ÂèëÈÄÅ'}
                    </Text>
                  </TouchableOpacity>
                </View>
              </View>
            )}

            {/* Ê≠•È™§3: ÂØÜÁ†ÅÈáçÁΩÆ */}
            {currentStep === 'reset' && (
              <View style={styles.stepContent}>
                <AuthInput
                  label="Êñ∞ÂØÜÁ†Å"
                  placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
                  value={formData.newPassword}
                  onChangeText={(value) => updateFormData('newPassword', value)}
                  error={errors.newPassword}
                  secureTextEntry={!showPassword}
                  icon="üîí"
                  rightIcon={showPassword ? "üëÅÔ∏è" : "üëÅÔ∏è‚Äçüó®Ô∏è"}
                  onRightIconPress={() => setShowPassword(!showPassword)}
                />

                <AuthInput
                  label="Á°ÆËÆ§Êñ∞ÂØÜÁ†Å"
                  placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
                  value={formData.confirmPassword}
                  onChangeText={(value) => updateFormData('confirmPassword', value)}
                  error={errors.confirmPassword}
                  secureTextEntry={!showConfirmPassword}
                  icon="üîí"
                  rightIcon={showConfirmPassword ? "üëÅÔ∏è" : "üëÅÔ∏è‚Äçüó®Ô∏è"}
                  onRightIconPress={() => setShowConfirmPassword(!showConfirmPassword)}
                />

                {/* ÂØÜÁ†ÅË¶ÅÊ±ÇÊèêÁ§∫ */}
                <View style={styles.passwordRequirements}>
                  <Text style={styles.requirementsTitle}>ÂØÜÁ†ÅË¶ÅÊ±ÇÔºö</Text>
                  <Text style={styles.requirementItem}>‚Ä¢ Ëá≥Â∞ë8‰ΩçÂ≠óÁ¨¶</Text>
                  <Text style={styles.requirementItem}>‚Ä¢ ÂåÖÂê´Â§ßÂÜôÂ≠óÊØç</Text>
                  <Text style={styles.requirementItem}>‚Ä¢ ÂåÖÂê´Â∞èÂÜôÂ≠óÊØç</Text>
                  <Text style={styles.requirementItem}>‚Ä¢ ÂåÖÂê´Êï∞Â≠ó</Text>
                </View>
              </View>
            )}

            {/* Êìç‰ΩúÊåâÈíÆ */}
            <AuthButton
              title={stepInfo.buttonText}
              onPress={stepInfo.onPress}
              loading={isLoading}
              style={styles.actionButton}
            />
          </Animated.View>

          {/* Â∫ïÈÉ®Â∏ÆÂä©‰ø°ÊÅØ */}
          <Animated.View
            style={[
              styles.footer,
              {
                opacity: fadeAnim,
                transform: [{ translateY: slideAnim }],
              },
            ]}
          >
            <View style={styles.helpContainer}>
              <Text style={styles.helpText}>
                ÈÅáÂà∞ÈóÆÈ¢òÔºü{' '}
                <Text style={styles.helpLink}>ËÅîÁ≥ªÂÆ¢Êúç</Text>
              </Text>
            </View>
          </Animated.View>
        </ScrollView>
      </KeyboardAvoidingView>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: colors.background,
  },
  keyboardAvoid: {
    flex: 1,
  },
  scrollView: {
    flex: 1,
  },
  scrollContent: {
    flexGrow: 1,
    paddingHorizontal: spacing.lg,
  },
  header: {
    paddingTop: spacing.lg,
    paddingBottom: spacing.xl,
  },
  backButton: {
    width: 40,
    height: 40,
    borderRadius: 20,
    backgroundColor: colors.surface,
    justifyContent: 'center',
    alignItems: 'center',
    marginBottom: spacing.lg,
    ...shadows.sm,
  },
  backButtonText: {
    fontSize: 20,
    color: colors.text,
  },
  titleContainer: {
    alignItems: 'center',
    marginBottom: spacing.lg,
  },
  title: {
    fontSize: fonts.size.header,
    fontWeight: 'bold',
    color: colors.text,
    marginBottom: spacing.sm,
  },
  subtitle: {
    fontSize: fonts.size.md,
    color: colors.textSecondary,
    textAlign: 'center',
    lineHeight: fonts.lineHeight.md,
  },
  progressContainer: {
    marginTop: spacing.lg,
  },
  progressBar: {
    height: 4,
    backgroundColor: colors.border,
    borderRadius: 2,
    overflow: 'hidden',
    marginBottom: spacing.md,
  },
  progressFill: {
    height: '100%',
    backgroundColor: colors.primary,
    borderRadius: 2,
  },
  stepIndicators: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  stepDot: {
    width: 32,
    height: 32,
    borderRadius: 16,
    backgroundColor: colors.border,
    justifyContent: 'center',
    alignItems: 'center',
  },
  stepDotActive: {
    backgroundColor: colors.primary,
  },
  stepDotText: {
    fontSize: fonts.size.sm,
    fontWeight: 'bold',
    color: colors.textSecondary,
  },
  stepDotTextActive: {
    color: colors.white,
  },
  formContainer: {
    flex: 1,
    paddingVertical: spacing.lg,
  },
  errorContainer: {
    backgroundColor: colors.error + '20',
    borderColor: colors.error,
    borderWidth: 1,
    borderRadius: borderRadius.md,
    padding: spacing.md,
    marginBottom: spacing.lg,
  },
  errorText: {
    color: colors.error,
    fontSize: fonts.size.sm,
    textAlign: 'center',
  },
  stepContent: {
    marginBottom: spacing.lg,
  },
  resendContainer: {
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: spacing.md,
  },
  resendText: {
    fontSize: fonts.size.sm,
    color: colors.textSecondary,
  },
  resendLink: {
    fontSize: fonts.size.sm,
    color: colors.primary,
    fontWeight: '500',
    textDecorationLine: 'underline',
  },
  resendLinkDisabled: {
    color: colors.textSecondary,
    textDecorationLine: 'none',
  },
  passwordRequirements: {
    backgroundColor: colors.surface,
    borderRadius: borderRadius.md,
    padding: spacing.md,
    marginTop: spacing.md,
    borderWidth: 1,
    borderColor: colors.border,
  },
  requirementsTitle: {
    fontSize: fonts.size.sm,
    fontWeight: '600',
    color: colors.text,
    marginBottom: spacing.sm,
  },
  requirementItem: {
    fontSize: fonts.size.xs,
    color: colors.textSecondary,
    marginBottom: spacing.xs,
  },
  actionButton: {
    marginTop: spacing.lg,
  },
  footer: {
    paddingVertical: spacing.xl,
    alignItems: 'center',
  },
  helpContainer: {
    alignItems: 'center',
  },
  helpText: {
    fontSize: fonts.size.sm,
    color: colors.textSecondary,
  },
  helpLink: {
    color: colors.primary,
    fontWeight: '500',
    textDecorationLine: 'underline',
  },
}); 